<template>
    <div>
        <v-app-bar id="appBar" clipped-left app dark :elevation="showScrollToTopFab?4:0"
                   :color="showScrollToTopFab?'primary':'transparent'">
            <v-tooltip right>
                <template v-slot:activator="{ on }">
                    <v-btn v-on="on" :light="!$vuetify.theme.dark&&drawer&&!showScrollToTopFab" icon
                           @click="drawer = !drawer">
                        <v-icon v-if="drawer">mdi-close</v-icon>
                        <v-icon v-else>mdi-table-of-contents</v-icon>
                    </v-btn>
                </template>
                <span>{{drawer?"关闭目录":"目录"}}</span>
            </v-tooltip>
            <v-toolbar-title>{{showScrollToTopFab?blogArticle.title:null}}</v-toolbar-title>
            <v-spacer/>
            <v-tooltip bottom>
                <template v-slot:activator="{ on }">
                    <v-btn v-on="on" icon @click="changeTheme()">
                        <v-icon v-if="!$vuetify.theme.dark">mdi-brightness-7</v-icon>
                        <v-icon v-else>mdi-brightness-4</v-icon>
                    </v-btn>
                </template>
                <span>{{$vuetify.theme.dark?"日间":"夜间"}}</span>
            </v-tooltip>
            <v-menu v-if="$vuetify.breakpoint.smAndDown" origin="top center" offset-overflow absolute
                    transition="scale-transition">
                <template v-slot:activator="{ on }">
                    <v-btn icon dark v-on="on">
                        <v-icon>mdi-cellphone-link</v-icon>
                    </v-btn>
                </template>
                <div style="width: 150px;height: 150px">
                    <VueQr :margin="10"
                           :text="API.UI_BASE_URL + $route.path.toString() + '?realPercentOfRead=' + realPercentOfRead + '&isDark=' + $vuetify.theme.dark"
                           style="width: 100%;height: 100%"/>
                </div>
            </v-menu>
            <v-tooltip v-else bottom>
                <template v-slot:activator="{ on }">
                    <div v-on="on">
                        <v-menu origin="top center" offset-overflow absolute transition="scale-transition">
                            <template v-slot:activator="{ on }">
                                <v-btn icon dark v-on="on">
                                    <v-icon>mdi-cellphone-link</v-icon>
                                </v-btn>
                            </template>
                            <div style="width: 150px;height: 150px">
                                <VueQr :margin="10"
                                       :text="API.UI_BASE_URL + $route.path.toString() + '?realPercentOfRead=' + realPercentOfRead + '&isDark=' + $vuetify.theme.dark"
                                       style="width: 100%;height: 100%"/>
                            </div>
                        </v-menu>
                    </div>
                </template>
                <span>在其他设备上继续阅读</span>
            </v-tooltip>
            <v-tooltip bottom>
                <template v-slot:activator="{ on }">
                    <v-app-bar-nav-icon v-on="on" @click="backOrGoToHome">
                        <v-icon>mdi-arrow-left</v-icon>
                    </v-app-bar-nav-icon>
                </template>
                <span>返回</span>
            </v-tooltip>
            <!--            <v-app-bar-nav-icon @click="backOrGoToHome">-->
            <!--                <v-icon>mdi-arrow-left</v-icon>-->
            <!--            </v-app-bar-nav-icon>-->
        </v-app-bar>
        <!--目录-->
        <v-navigation-drawer clipped app v-model="drawer" left :permanent="drawer && !$vuetify.breakpoint.smAndDown"
                             :temporary="$vuetify.breakpoint.smAndDown" mini-variant-width="200"
                             :color="$vuetify.breakpoint.smAndDown?'':'transparent'">
            <div v-if="loadingArticle">
                <v-skeleton-loader v-for="i in 15" :width="i%2===0||i%3===0?'200px':''" type="list-item"
                                   v-bind:key="i"/>
            </div>
            <v-treeview v-else open-all return-object dense :items="catalogue" activatable transition shaped
                        @update:active="updateActive" hoverable style="padding-bottom: 80px;"/>
        </v-navigation-drawer>
        <v-img :src="blogArticle.imgUrl===null?'/assets/header.jpg':blogArticle.imgUrl"
               style="height: 400px; margin-top: -70px" gradient="rgba(0,0,0,.1),rgba(0,0,0,.1)">
            <template v-slot:placeholder>
                <v-row class="fill-height ma-0" align="center" justify="center" style="background-color: #1976d2">
                    <v-progress-circular indeterminate color="grey lighten-5"/>
                </v-row>
            </template>
        </v-img>
        <!--文章标题-->
        <v-content class="ps-0">
            <v-card-title class="display-1 text-center justify-center white--text"
                          style="height: 300px; width: 100%; margin-top: -470px;font-weight: bold">
                {{blogArticle.title}}
            </v-card-title>
        </v-content>
        <!--文章详情-->
        <v-card id="card" :tile="$vuetify.breakpoint.smAndDown"
                :raised="!$vuetify.breakpoint.smAndDown"
                :flat="$vuetify.breakpoint.smAndDown"
                max-width="1000px" class="mx-auto"
                style="margin-top: -180px">
            <v-card flat class="pt-1">
                <!--详细内容-->
                <v-fade-transition>
                    <div v-if="loadingArticle" class="v-card__text">
                        <v-skeleton-loader max-width="500px" class="mb-10" type="text"/>
                        <v-skeleton-loader type="text" v-for="i in 10" :key="i"/>
                    </div>
                    <div v-else>
                        <v-card-text>
                            <strong class="font-weight-bold">友情提示：</strong>
                            此篇文章大约需要阅读<span style="color: #82b1ff">{{watchTimeString}}</span>，不足之处请多指教，感谢你的阅读。
                        </v-card-text>
                        <EditorMD :pre-set-content="blogArticle.content" :dark-when-read-only="$vuetify.theme.dark"/>
                    </div>
                </v-fade-transition>
                <!--赞赏-->
                <div id="likeFab"/>
                <v-skeleton-loader class="ma-2 d-flex justify-center align-center"
                                   v-if="loadingArticle" type="avatar"/>
                <div v-else-if="blogArticle.canReward" class="d-block text-center pt-5">
                    <v-tooltip bottom>
                        <template v-slot:activator="{ on }">
                            <div v-on="on">
                                <v-menu origin="center" nudge-left="45" nudge-top="75" transition="scale-transition">
                                    <template v-slot:activator="{ on }">
                                        <v-btn fab color="accent" v-on="on">
                                            <v-icon>mdi-thumb-up-outline</v-icon>
                                        </v-btn>
                                    </template>
                                    <v-img style="width: 150px" src="../assets/payCode.png"/>
                                </v-menu>
                            </div>
                        </template>
                        <span>赞赏</span>
                    </v-tooltip>
                </div>
                <!--时间-->
                <div v-if="loadingArticle" class="d-flex justify-end v-card__text">
                    <v-skeleton-loader width="180px" type="text, text"/>
                </div>
                <v-card-text v-else class="d-inline-block text-end">
                    发布于{{blogArticle.uploadTimeString}}<br/>
                    {{blogArticle.uploadTimeString===blogArticle.updateTimeString?null:"最后更新于"+blogArticle.updateTimeString}}
                </v-card-text>
                <!--转载声明-->
                <div v-if="loadingArticle">
                    <v-alert border="left" text class="ms-5 me-5" colored-border color="#e00000">
                        <div>
                            <v-skeleton-loader type="text,text,text" class="text-center"/>
                        </div>
                    </v-alert>
                </div>
                <div v-else class="pb-1">
                    <v-alert border="left" text class="text-break ms-5 me-5" colored-border color="#e00000">
                        <div v-if="blogArticle.original">
                            <strong>本文作者：</strong>苗锦洲<br/>
                        </div>
                        <strong>本文链接：</strong>{{API.UI_BASE_URL+$route.path}}<br/>
                        <div v-if="blogArticle.original">
                            <strong>版权声明：</strong>本博客所有文章除特别声明外，均采用
                            <a rel="noopener noreferrer" target="_blank"
                               href="https://creativecommons.org/licenses/by-sa/4.0/">CC BY-SA 4.0</a>
                            许可协议，转载请附上本文链接及本声明。
                        </div>
                    </v-alert>
                </div>
                <!--分类-->
                <div v-if="loadingArticle">
                    <v-divider/>
                    <div class="d-flex">
                        <v-icon class="pa-2">mdi-view-list</v-icon>
                        <v-skeleton-loader class="mt-1 mb-1" type="button"/>
                    </div>
                </div>
                <div v-else-if="blogArticle.typeString!==undefined && blogArticle.typeString!==null">
                    <v-divider/>
                    <div class="d-inline">
                        <v-icon class="pa-2">mdi-view-list</v-icon>
                        <v-btn text :to="{name:'/index/type',params:{paramType: blogArticle.typeString}}">
                            {{blogArticle.typeString}}
                        </v-btn>
                    </div>
                </div>
                <!--标签-->
                <div v-if="loadingArticle">
                    <v-divider/>
                    <div class="d-flex">
                        <v-icon class="pa-2">mdi-tag-multiple</v-icon>
                        <v-skeleton-loader class="mt-1 mb-1" type="chip"/>
                    </div>
                </div>
                <div v-else-if="blogArticle.tags!==undefined && blogArticle.tags.length">
                    <v-divider/>
                    <div class="d-flex">
                        <v-icon class="pa-2">mdi-tag-multiple</v-icon>
                        <v-chip-group column>
                            <v-chip v-for="tag in blogArticle.tags" :key="tag"
                                    :to="{name:'/index/tag',params:{'paramTag':tag}}">
                                {{tag}}
                            </v-chip>
                        </v-chip-group>
                    </div>
                </div>
            </v-card>
        </v-card>
        <!--文章切换-->
        <v-card tile id="cardBottomNavigationBar"
                :class="$vuetify.breakpoint.smAndDown?'d-flex ps-10 pe-10':'d-flex ps-10 pe-10 mt-10'">
            <v-row v-if="loadingPreAndNext" class="ms-10 me-10">
                <v-col class="pa-0" cols="6">
                    <v-card flat tile class="pa-5" style="width: 100%">
                        <v-icon class="d-inline">mdi-arrow-left</v-icon>
                        <v-card-subtitle class="d-inline pb-0">上一篇</v-card-subtitle>
                        <div class="ma-2">
                            <v-skeleton-loader type="text"/>
                            <div class="d-flex justify-start">
                                <v-skeleton-loader width="180px" type="text"/>
                            </div>
                        </div>
                    </v-card>
                </v-col>
                <v-col class="pa-0" cols="6">
                    <v-card flat tile class="pa-5 d-block" style="width: 100%;text-align: end">
                        <v-card-subtitle class="d-inline pb-0">下一篇</v-card-subtitle>
                        <v-icon class="d-inline">mdi-arrow-right</v-icon>
                        <div class="ma-2">
                            <v-skeleton-loader type="text"/>
                            <div class="d-flex justify-end">
                                <v-skeleton-loader width="180px" type="text"/>
                            </div>
                        </div>
                    </v-card>
                </v-col>
            </v-row>
            <v-row v-else class="ms-10 me-10">
                <v-col class="pa-0">
                    <v-card v-if="preArticle!==undefined && preArticle!==null" flat tile class="pa-5"
                            style="width: 100%"
                            @click="clickPreOrNextArticle(preArticle)">
                        <v-icon class="d-inline">mdi-arrow-left</v-icon>
                        <v-card-subtitle class="d-inline pb-0">上一篇</v-card-subtitle>
                        <v-card-title class="d-block pt-0 pb-0">{{preArticle.title}}</v-card-title>
                    </v-card>
                </v-col>
                <v-col class="pa-0">
                    <v-card v-if="nextArticle!==undefined && nextArticle!==null" flat tile class="pa-5 d-block"
                            style="width: 100%;text-align: end"
                            @click="clickPreOrNextArticle(nextArticle)">
                        <v-card-subtitle class="d-inline pb-0">下一篇</v-card-subtitle>
                        <v-icon class="d-inline">mdi-arrow-right</v-icon>
                        <v-card-title class="d-block pt-0 pb-0">{{nextArticle.title}}</v-card-title>
                    </v-card>
                </v-col>
            </v-row>
        </v-card>
        <Footer/>
        <v-snackbar v-model="snackbar">
            已自动恢复阅读位置、日/夜间模式参数
            <v-btn color="pink" text @click="$vuetify.goTo(0)">从头开始</v-btn>
        </v-snackbar>
        <v-fab-transition>
            <v-btn v-show="showScrollToTopFab" fab color="accent" class="ma-5"
                   style="position: fixed;right: 0;bottom: 0;z-index: 10" @click="$vuetify.goTo(0)">
                <v-progress-circular size="56" width="2" rotate="-90" :value="percentOfRead * 100">
                    <v-icon>mdi-chevron-up</v-icon>
                </v-progress-circular>
            </v-btn>
        </v-fab-transition>
    </div>
</template>

<script>
    import Footer from "@/components/constant/Footer"
    import VueQr from "vue-qr"
    import EditorMD from "@/components/admin/EditorMD";

    export default {
        name: "ArticleDetail",
        components: {EditorMD, Footer, VueQr},
        props: ["id"],
        data: vm => {
            return {
                blogArticle: {},
                showScrollToTopFab: false,
                drawer: vm.$vuetify.breakpoint.mdAndUp,
                catalogue: [],
                percentOfRead: 0.0,
                realPercentOfRead: 0.0,
                snackbar: false,
                loadingArticle: false,

                watchTimeString: "-",

                preArticle: null,
                nextArticle: null,
                loadingPreAndNext: false,
                paramsFromPre: {}
            }
        },
        methods: {
            //后退或者回到首页
            backOrGoToHome() {
                if (window.history.length <= 1) {
                    this.$router.push({path: '/'})
                    // return false
                } else {
                    this.$router.back()
                }
            },
            //恢复上一次阅读参数
            restoreLastReadProfile() {
                let _this = this
                setTimeout(function () {
                    //恢复是否为夜间模式
                    let dark = _this.$route.query.isDark
                    if (dark !== undefined) {
                        let isDark = dark === "true";
                        localStorage.setItem("theme", isDark.toString())
                        _this.$vuetify.theme.dark = isDark
                        _this.snackbar = true
                    }
                    //恢复阅读位置
                    let realPercentOfRead = _this.$route.query.realPercentOfRead
                    if (realPercentOfRead !== undefined && realPercentOfRead !== 0.0) {
                        //恢复阅读位置
                        let totalHeight = document.body.scrollHeight
                        // console.log(realPercentOfRead, realPercentOfRead * totalHeight)
                        _this.$vuetify.goTo(realPercentOfRead * totalHeight)
                        if (!_this.snackbar) {
                            _this.snackbar = true
                        }
                    }
                }, 1000)
            },
            getArticle: function () {
                this.loadingArticle = true
                // console.log(this.$route)
                this.initPreAndNextInfo()
                if (this.$route.params.articleFromPre !== undefined) {
                    this.blogArticle = this.$route.params.articleFromPre
                    this.axios.post(this.API.URL.INCREASE_ONE_CLICK_COUNT_ARTICLE + this.id)
                    this.loadingArticle = false
                    this.restoreLastReadProfile()
                } else {
                    let _this = this
                    this.axios.get(this.API.URL.QUERY_ARTICLE_BY_ID + this.id)
                        .then(function (result) {
                            // console.log(result.data)
                            if (result.data.success) {
                                if (result.data.data.deleted || !result.data.data.show) {
                                    _this.$router.replace({name: "404", params: {type: "文章"}})
                                } else {
                                    _this.blogArticle = result.data.data
                                    document.title = _this.blogArticle.title
                                    _this.restoreLastReadProfile()
                                }
                            } else {
                                _this.$router.replace({name: "404", params: {type: "文章"}})
                            }
                            _this.loadingArticle = false
                        }, () => {
                            _this.loadingArticle = false
                        })
                }
            },
            updateActive(array) {
                // console.log(array[0])
                // console.log(document.getElementsByName(array[0].name))
                this.$vuetify.goTo(document.getElementsByName(array[0].name)[0].parentElement)
            },
            handleScroll() {
                let totalHeight = document.body.scrollHeight
                // console.log("this.totalHeight", totalHeight)
                //滚动条偏移量
                let scrollTop = window.pageYOffset || document.documentElement.scrollTop || document.body.scrollTop
                //文章开始偏移量
                let cardOffsetTop = document.getElementById("card").offsetTop
                //文章结束偏移量
                let likeFabOffsetTop = document.getElementById("likeFab").offsetTop
                //是否显示滑倒顶部的FAB
                this.showScrollToTopFab = scrollTop >= cardOffsetTop
                // console.log("scrollTop", scrollTop, "cardOffsetTop", cardOffsetTop, "likeFabOffsetTop", likeFabOffsetTop, "totalHeight", totalHeight, "window.screen.height", window.screen.availHeight)
                //计算实际阅读百分比
                if (scrollTop >= cardOffsetTop) {
                    if (scrollTop + cardOffsetTop <= likeFabOffsetTop - cardOffsetTop) {
                        this.percentOfRead = ((scrollTop - cardOffsetTop) / (likeFabOffsetTop - cardOffsetTop - cardOffsetTop - cardOffsetTop)).toFixed(2)
                    } else {
                        this.percentOfRead = 1.0
                    }
                } else {
                    this.percentOfRead = 0.0
                }
                //计算网页百分比
                this.realPercentOfRead = (scrollTop) / totalHeight
                // console.log("this.percentOfRead", this.percentOfRead, "this.realPercentOfRead", this.realPercentOfRead)
            },
            /**
             *  初始化底部上/下一篇
             *  params:
             *      id: 7
             *      queryParamSearchString: undefined
             *      queryParamTag: ["共享元素动画", __ob__: Observer]
             *      queryParamType: undefined
             *
             */
            initPreAndNextInfo() {
                this.loadingPreAndNext = true
                let params = "";
                let search = this.$route.params.queryParamSearchString
                if (search !== undefined) {
                    this.paramsFromPre.search = search
                    params = params + "&search=" + search
                }
                let tags = this.$route.params.queryParamTag
                if (tags !== undefined) {
                    this.paramsFromPre.tags = tags
                    params = params + "&tags=" + tags
                }
                let types = this.$route.params.queryParamType
                if (types !== undefined) {
                    this.paramsFromPre.types = types
                    params = params + "&types=" + types
                }
                let _this = this
                this.axios.get(this.API.URL.QUERY_PRE_AND_NEXT + this.id + params).then(function (result) {
                    _this.loadingPreAndNext = false;
                    if (result.data.success) {
                        let data = result.data.data;
                        _this.preArticle = data.nextArticle;
                        _this.nextArticle = data.preArticle;
                    }
                }, () => {
                    _this.loadingPreAndNext = false
                })
            },
            clickPreOrNextArticle(article) {
                // console.log(this.paramsFromPre)
                let url = this.$router.resolve({
                    name: "article",
                    params: {
                        id: article.id
                        // , queryParamType: this.paramsFromPre.types,
                        // queryParamTag: this.paramsFromPre.tags,
                        // queryParamSearchString: this.paramsFromPre.search
                    }
                });
                window.open(url.href, "_blank")
            },
            changeTheme() {
                this.$vuetify.theme.dark = !this.$vuetify.theme.dark;
                localStorage.setItem('theme', this.$vuetify.theme.dark)
            }
        },
        created: function () {
            this.getArticle()
        },
        mounted: function () {
            //监听滚动事件，然后用handleScroll这个方法进行相应的处理
            window.addEventListener('scroll', this.handleScroll)
        },
        watch: {
            blogArticle: {
                handler(new_value) {
                    let content = new_value.content

                    //估算阅读时长
                    let totalSeconds = content.length / 8
                    let formatSeconds = this.UTIL.formatSeconds(totalSeconds);
                    this.watchTimeString = formatSeconds === "" ? "一瞬间~" : formatSeconds

                    let firstHeadCnt = 0
                    let headCnt = 0
                    let catalogues = []

                    let heads = content.match(/#+ .*/g)
                    if (heads !== null) {
                        heads.forEach(head => {
                            let headString = head.match(/(?<=# ).*/g)[0]
                            let index = head.indexOf("# ")
                            if (index === 0) {
                                catalogues[firstHeadCnt++] = {id: headCnt++, name: headString, children: []}
                            } else {
                                let indexTmp = index
                                //拿到一级标题的数组{id,name,children}
                                let tempCatalogues = catalogues
                                while (indexTmp > 0) {
                                    tempCatalogues = tempCatalogues[tempCatalogues.length - 1].children
                                    // console.log("indexTmp", indexTmp, "tempCatalogues", tempCatalogues)
                                    indexTmp--
                                }
                                // console.log("tempCatalogues", tempCatalogues)
                                tempCatalogues.push({id: headCnt++, name: headString, children: []})
                            }
                            // console.log(index, head, head.indexOf("# "))
                        })
                    }
                    this.catalogue = catalogues
                    // console.log(catalogues)
                    // let search3 = search2.match(/\[.*]*/g)
                    // console.log(search3)
                },
                deep: true
            }
        },
        destroyed() {
            //移除滚动事件，然后用handleScroll这个方法进行相应的处理
            window.removeEventListener('scroll', this.handleScroll, false)
        }
    }
</script>

<style scoped>

</style>